<template>
	<view class="content" :style="{'height':screenHeight}">
		<view class="topBox">
			<view class="names" v-for="(item,index) in titleData" :key="index" :class=" item.beThis == true ? 'special' : '' " @tap="titleBtn(index)">{{item.name}}</view>
		</view>
		<view class="clearBoth marginBottom-50"></view>
		
		<view class="allBox">
			<view class="boxes" v-for="(item,index) in orderData" :key="index" v-if="item.showThis == true">
				<view class="time">{{item.time}}</view>
				<image class="iconDel" src="../../../static/image/user/order/icon_del.png" mode="widthFix" v-if="item.status == 4" @tap="delBtn(index,item.id)"></image>
				<view class="splites" v-if="item.status == 4"></view>
				<view class="status" v-if="item.status == 1">待发货</view>
				<view class="status" v-if="item.status == 2">待收货</view>
				<view class="status" v-if="item.status == 3">待评价</view>
				<view class="status" v-if="item.status == 4">已完成</view>
				<view class="clearBoth marginBottom-40"></view>
				<image class="goodsImage" @tap="inGoods(index,item.ids)" :src="item.goodsImage" mode=""></image>
				<view class="name overflow_two" @tap="inGoods(index,item.ids)">{{item.introduce}}</view>
				<view class="right" @tap="inGoods(index,item.ids)">
					<view class="price">￥{{item.goods_price}}</view>
					<view class="num">* {{item.nums}}</view>
				</view>
				<view class="name_color overflow_two" @tap="inGoods(index,item.ids)">{{item.goods_attr_str}}</view>
				<view class="clearBoth"></view>
				<view class="payMent">
					<view class="priceNum">￥ {{item.payPrice}}</view>
					<view class="names">应付总额：</view>
				</view>
				<view class="clearBoth"></view>
				<view class="btns bg_white" @tap="btns(2,item.id,item)"		v-if="item.status == 2">查看物流</view>
				<view class="btns bg_blue" @tap="btns(0,item.sellsId,item)"	v-if="item.status == 4">再次购买</view>
				<view class="btns bg_blue" @tap="btns(1,item.id,item)"		v-if="item.status == 1">提醒发货</view>
				<view class="btns bg_blue" @tap="btns(4,item.id,item)"		v-if="item.status == 3">立即评价</view>
				<view class="btns bg_blue" @tap="btns(3,item.id,item)"		v-if="item.status == 2">确认收货</view>
				<!-- <view class="btns bg_false" @tap="btnFalse(index,item.id)">取消订单</view> -->
				<view class="clearBoth"></view>
			</view>
			<view class="clearBoth"></view>
			<view class="none fontSize14" v-if="orderData == ''">暂无数据</view>
		</view>

		
	</view>
</template>
<style>
	.content{background: rgb(244,244,244);width:100%;}
	.content .topBox{width:95%;padding-top:20upx;margin-left:2.5%;}
	.content .topBox .names{width:25%;font-size: 14px;border-bottom: 1px solid rgb(244,244,244);text-align: center;padding-bottom: 20upx;float: left;}/* rgb(119,188,233) */
	.content .topBox .special{color:rgb(119,188,233);border-bottom: 1px solid rgb(119,188,233);transition: all 0.1s;}
	.marginBottom-50{margin-bottom: 50upx;}
	.marginBottom-40{margin-bottom: 40upx}
	.allBox{width:100%;background-color: rgb(244,244,244)}
	.allBox .boxes{width: 90%;margin:20upx 0;;margin-left: 2.5%;padding:2.5%;background: white;border-radius: 10upx;}
	.allBox .boxes .time{font-size:14px;color:#B8B8B8;float: left;}
	.allBox .boxes .status{float: right;font-size: 14px;}
	.allBox .boxes .splites{float: right;margin:0 20upx;width:2upx;height:30upx;line-height: 30upx;margin-top: 7upx;border-right: 2upx dashed rgb(136,136,136);}
	.allBox .boxes .iconDel{float: right;width:27upx;height:29upx;position: relative;top:8upx}
	.allBox .boxes .goodsImage{width:170upx;height:170upx;margin-right:20upx;border-radius: 5upx;float: left;}
	.allBox .boxes .name{width:50%;float: left;font-size: 14px;color:#161616;}
	.allBox .boxes .name_color{width:50%;float: left;font-size: 12px;color:#989898;}
	.allBox .boxes .right{width:20%;float: left;font-size: 14px;color:#919191;;}
	.allBox .boxes .right .price{float: right;width: 100%;text-align: right;font-size: 14px;}
	.allBox .boxes .right .num{float: right;width:100%;text-align: right;font-size: 13px;color:#919191;margin-top: 15upx;}
	.allBox .boxes .payMent{float: right;margin:20upx 0;}
	.allBox .boxes .payMent .names{float: right;color:#161616;font-size:12px}
	.allBox .boxes .payMent .priceNum{float: right;color:#161616;font-size:14px}
	.allBox .boxes .btns{float: right;text-align: center;font-size: 14px;border-radius: 100upx;padding:10upx 35upx;font-size: 13px;margin-left:20upx;}
	.allBox .boxes .bg_blue{background:rgb(78,178,244);color:white;}
	.allBox .boxes .bg_white{;color:rgb(78,178,244);border: 1px solid rgb(78,178,244);}
	.allBox .boxes .bg_false{background:rgb(221,221,221);color:white;}
	
	.allBox .none{text-align: center;width:90%;padding:2.5%;margin-left: 2.5%;background: white;border-radius: 10upx;}
</style>
<script>
	export default {
		data() {
			return {
				chooseIndex:0,
				titleData:[
					{"name":"全部",beThis:true},
					{"name":"待发货",beThis:false},
					{"name":"待收货",beThis:false},
					{"name":"评价",beThis:false},
				],
				screenHeight:"",
				orderData:[
					//status == 1 是成功，有删除功能
					// {'id':1,'time':"2019-10-10",'status':1,'goodsImage':'../../../static/demo.jpg','introduce':'这是商品介绍1','goods_price':'10','nums':'1','payPrice':'100','showThis':true},
					// {'id':2,'time':"2019-10-11",'status':2,'goodsImage':'../../../static/demo.jpg','introduce':'这是商品介绍2','goods_price':'20','nums':'2','payPrice':'200','showThis':true}
				],
				pages:1,
				_$token:""
			}
		},
		onLoad(e){
			var that = this;
			const res = uni.getSystemInfoSync();
			this.getStorage();
			this.chooseIndex = e.index
			this.screenHeight = res.windowHeight*2 + 'upx'
			for (var i = 0; i < this.titleData.length; i++) {
				this.titleData[i].beThis = false
			}
			if( e.index == 0 ){
				this.titleData[0].beThis = true
			}else if( e.index == 1 ){
				this.titleData[1].beThis = true
			}else if( e.index == 2 ){
				this.titleData[2].beThis = true
			}else if( e.index == 3 ){
				this.titleData[3].beThis = true
			}
			setTimeout(function(){
				that.AjaxLoad();
			},100)
		},
		onReachBottom(){
			this.pages++;
			this.AjaxLoad();
			console.log( this.pages );
		},
		methods: {
			// getStorage(){var that = this;uni.getStorage({ key: 'token',success: function (res) {that._$token = res.data}})},
			getStorage(){var that = this;try {const value = uni.getStorageSync('token');if (value) {that._$token = value}} catch (e) {}},
			AjaxLoad(){
				var that = this;
				var type = ""
				var lists = []
				var status = ""
				if( this.titleData[0].beThis == true ){
					type = 1
				}else if( this.titleData[1].beThis == true ){
					type = 2
				}else if( this.titleData[2].beThis == true ){
					type = 3
				}else if( this.titleData[3].beThis == true ){
					type = 4
				}		
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"type":type,
					"page":this.pages,
				};
				this._$InitRequest.request('user/user_orderList', param).then(res => {
					res.data.data.forEach((item,index) =>{
						if( item.filter == 1 ){
							status = 1
						}else if( item.filter == 2 ){
							status = 2
						}else if( item.filter == 3 ){
							status = 3
						}else if( item.filter == 4 ){
							status = 4
						}
						lists.push({
							id:item.id,
							ids:item.goods_data.goods_id,
							sellsId:item.goods_data.goods_id,
							time:item.pay_time,
							status:status,
							goodsImage:item.goods_data.thumb_url,
							introduce:item.goods_data.goods_name,
							goods_price:item.goods_data.price,
							nums:item.goods_data.goods_num,
							goods_attr_str:item.goods_data.goods_attr_str,
							payPrice:item.total_price,
							showThis:true,
						})
					})
					for (var i = 0; i < lists.length; i++){
						that.orderData.push(lists[i])
					}
				},error=>{})
			},
			titleBtn(index){
				var that = this;
				var type = ""
				var lists = []
				var status = ""
				this.pages = 1;
				this.orderData = []
				for (var i = 0; i < this.titleData.length; i++) {
					this.titleData[i].beThis = false
				}
				this.titleData[index].beThis = true
				this.AjaxLoad();
			},
			delBtn(index,id){
				var that = this;
				let param={
					"user_id":this._$token.id,
					"user_token":this._$token.token,
					"order_id":id,
					"type":5
				};
				this._$InitRequest.request('user/order_operation', param).then(res => {
					if( res.data.code !== 200 ){
						uni.showToast({title:res.data.mess,icon:'none'})
						return;
					}else{
						uni.showToast({title:res.data.mess,icon:'none'})
						this.orderData[index].showThis = false
					}
				},error=>{})
			},
			inGoods(index,id){
				uni.navigateTo({url:"../../index/details/details?id=" + id})
			},
			// btnFalse(index,id){
			// 	console.log(index,'这是index');
			// 	console.log(id,'这是id');
			// },
			btns(e,id,item){
				var that = this;
				if( e == 0 ){
					uni.navigateTo({url:"../../index/details/details?id=" + id})
					return;
				}else if( e == 4 ){
					// uni.setStorage({
					//     key: 'commentOrder',
					//     data: item,
					//     success: function(){}
					// });
					try {
						uni.setStorageSync('commentOrder',item );
					}catch (e){}
					uni.navigateTo({url:"./comment/comment?id=" + id})
					return;
				}else if( e == 2 ){
					uni.navigateTo({
						url:"../socket_addr/socket_addr?id=" + id
					})
				}else{
					let param={
						"user_id":this._$token.id,
						"user_token":this._$token.token,
						"order_id":id,
						"type":e
					};
					this._$InitRequest.request('user/order_operation', param).then(res => {
						uni.showToast({title:res.data.mess,icon:'none'})
						if( e == 3 ){
							item.showThis = false
						}
					},error=>{})
				}
			},
			
			
			
			
		}
	}
</script>